img {
    object-fit: cover;
}

.game-board-front {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    background: #5454c6;
}

.game-board-front__row {
    width: 350px;
    height: 600px;
    border-radius: 10px;
    position: relative;
    overflow: hidden;
    z-index: 1;
    padding: 2rem;
}

*, *::before, *::after {
    box-sizing: border-box;
}

@-webkit-keyframes rotate {
    100% {
        transform: rotate(1turn);
    }
}

@keyframes rotate {
    100% {
        transform: rotate(1turn);
    }
}

.game-board-front__row::before {
    content: "";
    position: absolute;
    z-index: -2;
    left: -50%;
    top: -50%;
    width: 200%;
    height: 200%;
    background-color: #399953;
    background-repeat: no-repeat;
    background-size: 50% 50%, 50% 50%;
    background-position: 0 0, 100% 0, 100% 100%, 0 100%;
    background-image: linear-gradient(#399953, #399953), linear-gradient(#fbb300, #fbb300), linear-gradient(#d53e33, #d53e33), linear-gradient(#377af5, #377af5);
    -webkit-animation: rotate 4s linear infinite;
    animation: rotate 4s linear infinite;
}

.game-board-front__row::after {
    content: "";
    position: absolute;
    z-index: -1;
    left: 6px;
    top: 6px;
    width: calc(100% - 12px);
    height: calc(100% - 12px);
    background: white;
    border-radius: 5px;
}

.game-board-front__icon {
    position: absolute;
    width: 50px;
    z-index: 2;
}

.left-top {
    top: 10px;
    left: 10px;
}

.right-top {
    top: 10px;
    right: 10px;
}

.left-bottom {
    bottom: 10px;
    left: 10px;
}

.right-bottom {
    bottom: 10px;
    right: 10px;
}

.game-board-front__title {
    display: flex;
    justify-content: center;
    font-size: 30px;
    color: #399953;
    /*background: -webkit-linear-gradient(#08b631, #333);*/
    /*-webkit-background-clip: text;*/
    /*-webkit-text-fill-color: transparent;*/
    /*font-family: 'Bebas Neue', cursive;*/
}

.game-board-front__avatar-row {
    margin: auto;
    display: flex;
    justify-content: space-between;
}

.game-board-front__avatar-item {
    width: 80px;
    height: 112px;
}

.game-board-front__image {
    margin: 10px 0;
    display: flex;
    justify-content: center;
}

.game-board-front__image img {
    width: 150px;
    height: 175px;
}
