img {
    object-fit: cover;
}

.game-board-back {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    background: #5454c6;
}

.game-board-back__row {
    width: 350px;
    height: 600px;
    border-radius: 10px;
    position: relative;
    overflow: hidden;
    z-index: 1;
    padding: 2rem;
}

*, *::before, *::after {
    box-sizing: border-box;
}

@-webkit-keyframes rotate {
    100% {
        transform: rotate(1turn);
    }
}

@keyframes rotate {
    100% {
        transform: rotate(1turn);
    }
}

.game-board-back__row::before {
    content: "";
    position: absolute;
    z-index: -2;
    left: -50%;
    top: -50%;
    width: 200%;
    height: 200%;
    background-color: #399953;
    background-repeat: no-repeat;
    background-size: 50% 50%, 50% 50%;
    background-position: 0 0, 100% 0, 100% 100%, 0 100%;
    background-image: linear-gradient(#399953, #399953), linear-gradient(#fbb300, #fbb300), linear-gradient(#d53e33, #d53e33), linear-gradient(#377af5, #377af5);
    -webkit-animation: rotate 4s linear infinite;
    animation: rotate 4s linear infinite;
}

.game-board-back__row::after {
    content: "";
    position: absolute;
    z-index: -1;
    left: 6px;
    top: 6px;
    width: calc(100% - 12px);
    height: calc(100% - 12px);
    background: white;
    border-radius: 5px;
}

.game-board-back__icon {
    position: absolute;
    width: 50px;
    z-index: 2;
}

.left-top {
    top: 10px;
    left: 10px;
}

.right-top {
    top: 10px;
    right: 10px;
}

.left-bottom {
    bottom: 10px;
    left: 10px;
}

.right-bottom {
    bottom: 10px;
    right: 10px;
}

.game-board-back__text {
    width: 300px;
    position: absolute;
    /* Dịch chuyển 50% theo theo phần tử định vị*/
    top: 50%;
    left: 50%;
    /* Dịch chuyển ngược lại 50% theo chiều rộng và chiều cao */
    transform: translate(-50%, -50%);

}

.game-board-back__content {
    width: 100%;
    margin: auto;
    margin-top: 10px;
    text-align: center;
    font-size: 20px;
    color: #399953;
    font-family: 'Anton'!important;
}


.game-board-back__drink {
    width: 100%;
    margin: auto;
    margin-top: 10px;
    text-align: center;
    font-size: 20px;
    color: #e569ca;
    font-family: 'Alfa Slab One', cursive;
}

.game-board-back__end {
    width: 100%;
    margin: auto;
    margin-top: 10px;
    text-align: center;
    font-size: 20px;
    color: #1f1fe1;
    font-family: 'Francois One', sans-serif;
    font-style: italic;
}

.game-board-back__image {
    max-width: 200px;
    max-height: 200px;
    display: flex;
    margin: auto;
}